<style lang=less>
    @import '../../../assets/index.less';
    @import './identity.less';
</style>
<template>
    <div class="base-info identity">
        <p class="title">
            <span>
            </span>
            {{$t('center.identity[0]')}}
            <i>
                （*  {{$t('center.identity[1]')}}）
            </i>
        </p>
        <div class="identity-box" @click="goLowerIdentity">
            <img class="box-left" src="../../../assets/img/center/identity1.png"/>
            <div class="box-center">
                <p class="box-center-top">
                    {{$t('center.identity[2]')}}
                    <span>
                        （{{lowerIdentityText}}）
                    </span>
                </p>
                <p class="box-center-bottom">
                  {{$t('center.identity[3]')}}
                </p>
            </div>
            <span class="box-right">
            </span>
        </div>
        <div class="identity-box" @click="goHeigherIdentity" style="margin-left: 75px;">
            <img class="box-left" src="../../../assets/img/center/identity2.png"/>
            <div class="box-center">
                <p class="box-center-top">
                  {{$t('center.identity[4]')}}
                    <span>
                        （{{heigherIdentityText}}）
                    </span>
                </p>
                <p class="box-center-bottom">
                  {{$t('center.identity[5]')}}
                </p>
            </div>
            <span class="box-right">
            </span>
        </div>
    </div>
</template>
<script>
import {userInfo} from '@/api/public.js'
export default {
  data () {
    return {
      lowerIdentityText: this.$t('center.identity[6]'),//初级身份验证的文字
      heigherIdentityText: this.$t('center.identity[6]'),//高级身份验证的文字
      ableGoLower: false,
      ableGoHeigher: false
    }
  },
  created () {
    userInfo().then(res => {
      if(res.data.status == 200){
        let status = res.data.data.status
        let heigherStatus = res.data.data.auth_status
        if(status == 1){ //初级身份验证
          this.lowerIdentityText = this.$t('center.identity[6]')
          this.ableGoLower = true
          this.ableGoHeigher = false
        } else if(status == 2){
          this.lowerIdentityText = this.$t('center.identity[7]')
          this.ableGoLower = false
          this.ableGoHeigher = false
        } else if(status == 3){
          this.lowerIdentityText = this.$t('center.identity[8]')
          this.ableGoLower = false
          this.ableGoHeigher = true
        } else if(status == 4){
          this.lowerIdentityText = this.$t('center.identity[9]')
          this.ableGoLower = true
          this.ableGoHeigher = false
        }

        if(heigherStatus == 1 && status == 3){ //高级身份验证
          this.heigherIdentityText = this.$t('center.identity[6]')
          this.ableGoLower = false
          this.ableGoHeigher = true
        } else if(heigherStatus == 2 && status == 3){
          this.ableGoLower = false
          this.ableGoHeigher = false
          this.heigherIdentityText = this.$t('center.identity[7]')
        } else if(heigherStatus == 3 && status == 3){
          this.ableGoLower = false
          this.ableGoHeigher = false
          this.heigherIdentityText = this.$t('center.identity[8]')
        } else if(heigherStatus == 4 && status == 3){
          this.ableGoLower = false
          this.ableGoHeigher = true
          this.heigherIdentityText = this.$t('center.identity[9]')
        }
      }
    })
  },
  watch: {
  },
  methods: {

    goLowerIdentity(){//跳转至低级认证
      if(!this.ableGoLower){return}
      this.$router.push({
        path: "/center/identity/lowerIdentity",
      })
    },
    goHeigherIdentity(){//跳转至高级认证
      if(!this.ableGoHeigher){return}
      this.$router.push({
        path: "/center/identity/heigherIdentity",
      })
    }
  }
}
</script>



